<template>
  <router-view v-if="this.$route.name !== 'Channel'" />
  <div class="app-container" v-else>
    <div class="filter-container">
      <el-input
        v-model="listQuery.name"
        placeholder="名称"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-cascader
        :props="typeOptions"
        v-model="listQuery.accessPointId"
        placeholder="接入点"
        clearable
        class="filter-item"
      >
      </el-cascader>
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        搜索
      </el-button>
      <router-link
        :to="{ name: 'CreateChannel', params: { supplierId: this.listQuery.supplierId } }"
      >
        <el-button
          class="filter-item"
          style="margin-left: 10px;"
          type="primary"
          icon="el-icon-edit"
        >
          添加
        </el-button>
      </router-link>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="ID" width="75">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="名称" align="center">
        <template slot-scope="scope">
          {{ scope.row.name }}
        </template>
      </el-table-column>
      <el-table-column label="编码" align="center">
        <template slot-scope="scope">
          {{ scope.row.code }}
        </template>
      </el-table-column>
      <el-table-column label="方法" align="center">
        <template slot-scope="scope">
          {{ scope.row.method }}
        </template>
      </el-table-column>
      <el-table-column label="单价(元)" width="110" align="center">
        <template slot-scope="scope">
          <el-tag :type="scope.row.price | priceFilter">{{
            parseFloat(scope.row.price) / 100.0
          }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column class-name="status-col" label="状态" align="center">
        <template slot-scope="scope">
          {{ statusMap[scope.row.status] }}
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row }">
          <router-link
            :to="{
              name: 'editChannel',
              params: { id: row.id }
            }"
          >
            <el-button size="mini" type="primary">
              编辑
            </el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
  </div>
</template>

<script>
import { list, accessType } from "@/api/accessPoint";
import { channelList } from "@/api/supplier";
import Pagination from "@/components/Pagination";
import waves from "@/directive/waves"; // waves directive

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    priceFilter(price) {
      let val = "success";
      if (price === 0) {
        val = "warning";
      } else if (price < 0) {
        val = "danger";
      }
      return val;
    }
  },
  data() {
    return {
      total: 0,
      codeOptions: [],
      accessPointOptions: [],
      listQuery: {
        name: "",
        supplierId: undefined,
        accessPointId: undefined,
        pageNo: 1,
        pageSize: 20
      },
      typeOptions: {
        emitPath: false,
        lazy: true,
        lazyLoad(node, resolve) {
          const { level } = node;
          if (level === 0) {
            accessType({ status: 1 }).then(response => {
              if (response.code === 0) {
                this.codeOptions = response.data.record;
                const nodes = this.codeOptions.map(item => ({
                  value: item.id * -1,
                  label: item.name,
                  leaf: false
                }));
                resolve(nodes);
              }
            });
          } else {
            let id = node.data.value * -1;
            list({ status: 1, accessPointTypeId: id }).then(response => {
              if (response.code === 0) {
                this.codeOptions = response.data.record;
                const nodes = this.codeOptions.map(item => ({
                  value: item.id,
                  label: item.name,
                  leaf: true
                }));
                resolve(nodes);
              }
            });
          }
        }
      },
      list: null,
      listLoading: true,
      statusMap: {
        1: "正常",
        0: "停用"
      }
    };
  },
  created() {
    this.listQuery.supplierId = this.$route.params && this.$route.params.supplierId;
    if (!this.listQuery.supplierId) {
      this.$router.push("/supplier");
    }
    this.fetchData();
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      channelList(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
        }
        this.listLoading = false;
      });
    },
    handleFilter() {
      this.fetchData();
    }
  }
};
</script>
